<template>
    <div class="my-profile-main">
    <el-card class="profile-content">
        <div class="user">
        <img :src="initData.image" alt="User Image" class="user-image">
        <div class="name">
            <p class="username">{{ initData.name }}</p>
            <p class="email">{{ initData.email }}</p>
        </div>
        </div>
        <div class="information">
        <p>我的留言板: {{ initData.posts_count }}</p>
        <p>我的评论 :  {{ initData.comments_count }}</p>
        <p>管理员权限: {{ initData.admin === 0 ? "无" : "是" }}</p>
        <p>上次登录: {{ change_dateStr_last(initData.last_time) }}</p>
        <p>生日: {{ change_dateStr_birthday(initData.create_time) }}</p>
        </div>
        <div class="update-information">
        <el-button type="primary" @click="update_profile">编辑个人资料</el-button>
        </div>
    </el-card>
    </div>
</template>
<script>
import {ref,onMounted,} from 'vue';
import {get} from '@/http/httpRequest';
import { notice } from '@/components/notice';
import { useUrl } from '@/store/useUrl';
import { useRouter } from 'vue-router';
export default {
    setup(){
        const initData=ref({});
        const urls=useUrl();
        const router=useRouter();

        const initPage=async ()=>{
            const url=urls.user;
            const response=await get(url);
            if(response){
                initData.value=response.data;
            }else{
                notice("出错了","",1500);
            }
        }

        const update_profile=()=>{
            router.push({path:"/blank"});
        }

        const change_dateStr_birthday=(str)=>{
            try{
                const date=new Date(str);
                const month = date.getUTCMonth() + 1; 
                const day = date.getUTCDate();
                return month+'-'+day;

            }catch(error){
                return str;
            }
        }
        const change_dateStr_last=(str)=>{
            try{
                const date=new Date(str);
                const day=date.getUTCDate();
                const hours = date.getUTCHours();
                const minutes = date.getUTCMinutes();
                return day+'-'+hours+'-'+minutes

            }catch(error){
                return str;
            }
        }

        onMounted(async ()=>{
            await initPage();
        });
        return{
            initData,
            update_profile,
            change_dateStr_birthday,
            change_dateStr_last
        }
    }
}
</script>

